<style>
    #main {
        margin: auto;
    }
    #bot-pads {
        display: grid;
        grid-template-columns: repeat(9, 1fr);
    }
    #top-pads {
        display: grid;
        grid-template-columns: .5fr 1fr .5fr repeat(7, 1fr);
    }
    .thing{
        height: 2rem;
        margin-top: 5rem;
        width: 95%;
    }
    .left-thing{
        border-top: solid;
        border-left: solid;
    }
    .right-thing{
        border-top: solid;
        border-right: solid;
    }
    button{
        border-style: solid;
        border-color: black;
        border-radius: .5rem;
        /* height: 7rem; */
    }
    .half{
        height: 3em;
        margin-top: 5em;
    }
  </style>
<script>
    import Pad from "./pad.svelte";
    let muteArray = Array(10).fill(false);//false = unmuted part
    export let muteCount = 0;
    export let muteState = 0;
    let names = ['SYNTH 1', 'SYNTH 2', 'SYNTH 3', 'SYNTH 4', 'AUDIO 1', 'AUDIO 2', 'CLOSE HH', 'OPEN HH']
    let partBits = {'SYNTH 1': 1<<8, 'SYNTH 2': 1<<9, 'SYNTH 3': 1<<10, 'SYNTH 4': 1<<11, 'AUDIO 1': 1<<4, 'AUDIO 2': 1<<5, 'CLOSE HH': 1, 'OPEN HH': 1<<1, 'CRASH': 1<<2, 'CLAP': 1<<3}//bit shifting to get mute values in correct spots
    $: { 
        muteCount = 0;
        for (let i = 0; i < muteArray.length; i++){
            if (muteArray[i]){
            muteCount += 1;
        }
        muteState = 0;
        for (let i = 0; i < names.length; i++){
            muteState += partBits[names[i]]*(!muteArray[i]);
        }
        muteState += partBits['CLAP']*(!muteArray[9]) + partBits['CRASH']*(!muteArray[8]);
        console.log(muteState);
    }
}

</script>
<div id="main">

  <div id="top-pads">
      <div class="left-thing thing"></div>
      <button class="half">RING MOD</button>
      <div class="right-thing thing"></div>
      <div></div>
      <div class="left-thing thing"></div>
      <button class="half">RING MOD</button>
      <div class="right-thing thing"></div>
      <Pad bind:muted={muteArray[8]} name="CRASH"></Pad>
      <Pad bind:muted={muteArray[9]} name="CLAP"></Pad>
  </div> 

  <div id="bot-pads">
      {#each names as part, i}
    <Pad bind:muted={muteArray[i]} name={part}></Pad>
    {/each}
    <button>ACC</button>
  </div>

</div>