<style>
	#main{
		width: clamp(300px, 90%, 600px);
		margin: auto;
	}
	
	h1{
		border: solid;
		border-color: white;
		text-align: center;
		width: 90%;
		margin: auto;
		padding-left: 1rem;
		padding-right: 1rem;
	}
	h2{
		margin-left: 5%;
	}

ul {
	border: 2px solid white;
	border-radius: 5px;
	padding: 10px;
	width: 10rem;
	margin-left: 5%;
}
li{
	list-style: none;
	font-size: 1rem;
}

#info-boxes{
	display: flex;
	flex-wrap: wrap;
	width: 100%;
}

.info{
	margin-left: 5%;
}


</style>

<script>
import Pads from './pads.svelte'
	let topNum = 0;
	let bytes = 0;
	let byteHigh;
	let byteLow;
	$: {
		byteHigh = bytes >> 8; //bitshift right until the lower byte is gone
		byteLow = bytes & 255; // multiply with 11111111 to only have lower byte remaining
	}
</script>

<div id="main">
	<h1>ELECTRIBE ER-1 MUTE CALCULATOR</h1>
	<h2>{topNum} parts are muted</h2><br>
	<h2>Send the following NRPN midi messages for current mute state: </h2>
<div id="info-boxes">
	<ul>
		<li>0xB9, 0x63, 0x02</li>
		<li>0xB9, 0x62, 0x6D</li>
		<li>0xB9, 0x06, 0x0{byteHigh.toString(16).toUpperCase()}</li>
		<br>
		<li>0xB9, 0x63, 0x02</li>
		<li>0xB9, 0x62, 0x6E</li>
		<li>0xB9, 0x06, 0x{byteLow.toString(16).toUpperCase()}</li>
	</ul>
	<div class="info">
		<p>Bin representation: {(bytes + (1 << 16)).toString(2).slice(1)}</p>
		<!-- bitshift and remove after conversion to string in order to preserve leading zeroes -->
		<p>Hex representation: 0x0{bytes.toString(16).toUpperCase()}</p>
	</div>
</div>
<Pads bind:muteCount={topNum} bind:muteState={bytes}></Pads>
</div>