<style>
button {
    height: 7rem;
    border: solid;
    border-radius: .5rem;
    font-weight: 900;
    }
button:hover{
    cursor: pointer;
}
.muted{
    background-color: #333;
    color: gray;
}
</style>

<script>
    export let muted = false;
    export let name = 'pad';
    function handleClick(){
        muted = !muted;
    }
</script>

<button class:muted={muted} on:click="{handleClick}">
{name}
</button>